<template>
  <div id="myPage" class="container">
    <div class="myPage-box">
      <h1> </h1>
      <div class="information">
        <p><strong>:</strong>{{myInfo.nickname}}</p>
        <p><strong>: </strong>{{myInfo.lastName}}{{myInfo.firstName}}</p>
        <p><strong>: </strong>{{myInfo.avatar}}</p>
        <p><strong>: </strong>{{myInfo.age}}</p>
        <p><strong>: </strong>{{myInfo.address}}</p>
        <p><strong>:</strong> {{myInfo.phone_first}}-{{myInfo.phone_middle}}-{{myInfo.phone_last}}</p>
      </div>

      <button @click="editUser" class="btn btn-default"> </button>
      <button @click="deleteUser" class="btn btn-default"></button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      myInfo: {}
    }
  },
  methods: {
    editUser() {
      this.$router.push('/' + this.$route.params.id + '/myPage/edit')
    },
    deleteUser() {
      if(confirm(' ?') == true) {
        this.$http.delete('/api/login/' + this.$route.params.id + '/myPage')
        .then((response) => {
          if(response.data.result == 'success') {
            alert(' .');
            this.$store.dispatch('logout');
            this.$router.push('/login');
          } else if(response.data.result == 'fail') {
            alert(' .')
          }
        })
        .catch(error => {
          alert(error)
        })
      }
        
    }
  },
  created() {
    this.$http.get('/api/login/' + this.$route.params.id + '/myPage')
    .then((response) => {
      this.myInfo = response.data;
    })
    this.$store.dispatch('tryAutoLogin')
      .then((response) => {
        console.log('tryAutoLogin');
        console.log(typeof this.$store.getters.isAdmin);
      });
  }
}
</script>

<style src="./user.css">
</style>
